body {
  overflow-y: scroll;
}
.inputs {
  margin-top: 20px;
}
/* width */
::-webkit-scrollbar {
  width: 12px;
}
/* Track */
::-webkit-scrollbar-track {
  background: var(--gray);
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--dark);
  border-radius: 6px 6px 6px 6px / 12px 12px 12px 12px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
























































































































































































































/* highlight selected row */
.b-table-has-details {
  background: var(--gray);
  color: var(--white);
}
.b-table-has-details + tr {
  background: var(--gray);
}
